.App {
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: rgb(239 239 239);
  min-height: 90vh;
  /* display: flex; */
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* font-size: calc(10px + 2vmin); */
  color: #183b56;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.snacks-title {
  font-size: 50px;
  font-weight: 700 !important;
}

.snacks-number span {
  font-size: 25px !important;
  background-color: #075fff;
  border-radius: 7px;
  font-weight: 700;
  position: absolute;
  top: -10px;
  left: 20px;
  width: 50px;
  height: 50px;
  text-align: center;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.snacks-description p {
  text-align: left;
  font-size: 16px;
  margin-left: 8px;
}

.snacks-description h3::before {
  content: "";
  display: inline-block;
  width: 4px;
  height: 25px;
  background-color: #ef6038;
  margin: 0px 8px -2px -2px;
  border-radius: 2px;
}

.snacks-description {
  padding: 2x !important;
}

.layer {
  /* background-color: rgba(0, 0, 0, 0.6); */
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 6%,
    rgba(9, 9, 121, 0) 48%,
    rgba(0, 212, 255, 0) 100%
  );
  /* position: absolute; */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 1rem;
}

.emp span {
  border-bottom: 1px solid #ef6038;
}

/* buttons */
.btn-snacks {
  background-color: #ef6038;
  border-color: #ef6038;
}

.btn-snacks:hover {
  background-color: #dd522b;
  border-color: #dd522b;
}

.btn-snacks:focus {
  background-color: #dd522b;
  border-color: #dd522b;
  box-shadow: 0 0 0 3px #dd522b3b !important;
}

.btn-snacks:active {
  background-color: #dd522b !important;
  border-color: #dd522b !important;
  outline-color: #dd522b !important;
  box-shadow: 0 0 0 3px #dd522b41 !important;
}

.btn-snacks-blue {
  background-color: rgb(7, 95, 255);
  border-color: rgb(7, 95, 255);
}

.btn-snacks-blue:hover {
  background-color: rgb(5, 81, 223);
  border-color: rgb(5, 81, 223);
}

.btn-snacks-blue:focus {
  background-color: rgb(5, 81, 223);
  border-color: rgb(5, 81, 223);
  box-shadow: 0 0 0 3px rgba(5, 81, 223, 0.164) !important;
}

.btn-snacks-blue:active {
  background-color: rgb(5, 81, 223) !important;
  border-color: rgb(5, 81, 223) !important;
  box-shadow: 0 0 0 3px rgba(5, 81, 223, 0.164) !important;
}

.modal-content {
  width: 100% !important;
  text-align: center;
  margin-right: 2rem;
}

.modal-dialog {
  float: right;
  margin-right: 2rem;
}
